html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to right, #00bfb2, #00cb70);
}

.logo {
  width: 130px;
  height: auto;
  position: fixed;
  top: 25px;
  left: 25px;
}

h2 {
  color: white;
  font-size: 3rem;
  font-weight: bold;
}

.main-container {
  max-width: 800px;
  margin: auto;
}

.q-container {
  max-width: 100%;
  margin-top: 35%;
}

.grid-container {
  color: white;
  margin-top: 50px;
  max-width: 100%;
  display: flex;
  flex-direction: row;
}

.grid-option {
  border: 2px solid white;
  font-size: 1.4rem;
  font-weight: bold;
  border-radius: 10px;
  padding: 15px;
  width: 200px;
  margin-right: 30px;
  opacity: 0.8;
}

.grid-option:hover {
  opacity: 1;
  background-color: rgba(255,255,255, 0.5);
  border: 2px solid rgba(255,255,255, 0.5);
  transition: ease background-color 0.1s;

}

.column-container {
  color: white;
  margin-top: 50px;
  max-width: 100%;
}

.column-option {
  border: 2px solid white;
  font-size: 1.4rem;
  font-weight: bold;
  border-radius: 10px;
  padding: 15px;
  margin-right: 30px;
  opacity: 0.8;
  margin-bottom: 15px;
}

.column-option:hover {
  opacity: 1;
  background-color: rgba(255,255,255, 0.5);
  border: 2px solid rgba(255,255,255, 0.5);
  transition: ease background-color 0.1s;

}

.text-answer {
  margin-top: 50px;
  color: white;
  outline: none;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 3px solid rgba(255,255,255,0.5) !important;
  padding-bottom: 9px;
  background: none;
  font-size: 1.8rem;
  font-weight: bold;
  width: 800px;
}

.help-text-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  width: 300px;
  float: right;
  margin-top: 10px;
  font-size: 1.1rem;
}

.unselected {
  opacity: 0.5;
}

.help-text {
  font-weight: bold;
  color: white;
}

::placeholder {
  color: white;
  font-size: 1.8rem;
  opacity: 0.5;
}

.highlighted {
  color: white;
  border-radius: 10px;
  padding-left: 8px;
  padding-right: 8px;
  padding-bottom: 1px;
  background-color: rgba(34, 181, 115, 1);
  font-weight: bold;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
